<script setup lang="ts">
import { ref, watch } from 'vue';
import { ElMessage, ElForm } from 'element-plus';
import { save } from "@/api/api.ts";
import QRCode from 'qrcode';

const visible = ref(false);
const formRef = ref<InstanceType<typeof ElForm>>();

const form = ref({
  id: 0,
  channel_name: "",  // 通道名称
  product_code: "",  // 产品编号
  payment_amount: 0,  // 支付金额(元)
});

// 测试结果相关数据
const testResult = ref({
  success: false,
  message: "",
  request_params: {},  // 请求参数
  channel_response: {},  // 通道响应
  qr_code_url: "",  // 二维码URL
});

const qrCodeUrl = ref("");  // 用来保存二维码图片的 URL

// 表单验证规则
const rules = ref({
  channel_name: [{ required: true, message: '请输入通道名称', trigger: 'blur' }],
  payment_amount: [
    {
      required: true,
      message: '请输入支付金额',
      trigger: 'blur',
      validator: (_rule: any, value: any, callback: any) => {
        if (!value || isNaN(Number(value)) || Number(value) <= 0) {
          callback(new Error('请输入有效的支付金额'));
        } else {
          callback();
        }
      },
    },
  ],
});

// 打开抽屉并加载数据
const open = async (data = {}) => {
  // 先重置表单和测试结果
  resetForm();
  resetTestResult();

  const merged = { id: 0, ...data };
  Object.assign(form.value, merged);
  visible.value = true;
};

// 提交表单
const handleSubmit = async () => {
  await formRef.value?.validate();

  let submitData: any = {};
  try {
    // 重置测试结果
    resetTestResult();
    // 准备提交数据，确保格式正确
    const amount = Number(form.value.payment_amount);
    submitData = {
      channel_id: form.value.id || 0,
      channel_name: form.value.channel_name?.trim?.() || form.value.channel_name,
      product_code: form.value.product_code?.trim?.() || form.value.product_code,
      payment_amount: amount > 0 ? amount : 1,
    };

    // 移除空值字段
    Object.keys(submitData).forEach(key => {
      if (submitData[key] === undefined || submitData[key] === '' || submitData[key] === null) {
        delete submitData[key];
      }
    });

    let res: any = await save('/api/v1/admin/payment-channel/test', submitData);
    
    // 根据 data.data.status 判断测试是否成功
    const isSuccess = res.data?.data?.status === true;
    
    // 更新测试结果
    testResult.value = {
      success: isSuccess,
      message: res.data?.data?.message || res.data?.message || res.message || (isSuccess ? '测试成功' : '测试失败'),
      request_params: res.data?.header || {},
      channel_response: res.data?.raw_response?.original_response || res.data?.data || {},
      qr_code_url: res.data?.data?.qr_code || res.data?.url || "",
    };

    if (isSuccess) {
      ElMessage.success(res.data?.data?.message || res.data?.message || res.message || '测试成功');

      // 如果有二维码URL，生成二维码
      if (res.data?.data?.qr_code || res.data?.url) {
        generateQRCode(res.data?.data?.qr_code || res.data?.url);
      }
    } else {
      ElMessage.error(res.data?.data?.message || res.data?.message || res.message || '测试失败');
    }
  } catch (error: any) {
    console.error('测试失败:', error);
    testResult.value = {
      success: false,
      message: '测试失败：' + (error.message || '未知错误'),
      request_params: submitData,
      channel_response: {},
      qr_code_url: "",
    };
    ElMessage.error('测试失败');
  }
};

// 生成二维码
const generateQRCode = (url: string) => {
  QRCode.toDataURL(url, (err, url) => {
    if (err) {
      ElMessage.error('生成二维码失败');
      console.error(err);
      return;
    }
    qrCodeUrl.value = url;  // 保存二维码图片的 URL
  });
};

// 重置表单数据
const resetForm = () => {
  form.value = {
    id: 0,
    channel_name: "",
    product_code: "",
    payment_amount: 1,
  };
};

// 重置测试结果
const resetTestResult = () => {
  testResult.value = {
    success: false,
    message: "",
    request_params: {},
    channel_response: {},
    qr_code_url: "",
  };
  qrCodeUrl.value = "";
};

// 监听 visible 变化，关闭时重置表单
watch(visible, (newVal, oldVal) => {
  if (oldVal === true && newVal === false) {
    resetForm();
    resetTestResult();
  }
});

defineExpose({ open });
</script>

<template>
  <el-drawer v-model="visible" title="通道测试" size="70%">
    <el-form :model="form" :rules="rules" ref="formRef" label-width="120px">
      <el-divider content-position="left">测试参数</el-divider>

      <!-- 通道信息 -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="通道名称" prop="channel_name">
            <el-input v-model="form.channel_name" placeholder="请输入通道名称" />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="产品编号">
            <el-input v-model="form.product_code" placeholder="产品编号不可编辑" disabled />
          </el-form-item>
        </el-col>
      </el-row>

      <!-- 金额 -->
      <el-form-item label="金额(元)" prop="payment_amount">
        <el-input v-model="form.payment_amount" placeholder="请输入测试金额">
          <template #append>元</template>
        </el-input>
      </el-form-item>

      <!-- 提交按钮 -->
      <el-form-item>
        <el-button type="primary" @click="handleSubmit" :loading="false">开始测试</el-button>
      </el-form-item>
    </el-form>

    <!-- 测试结果显示区域 -->
    <div class="test-result-container">
      <el-divider content-position="left">测试结果</el-divider>

      <!-- 测试结果文本域 -->
      <el-form-item label="测试结果">
        <el-input
            v-model="testResult.message"
            type="textarea"
            :rows="3"
            placeholder="测试结果将显示在这里"
            readonly
        />
      </el-form-item>

      <!-- 请求参数文本域 -->
      <el-form-item label="请求参数">
        <el-input
            :value="JSON.stringify(testResult.request_params, null, 2)"
            type="textarea"
            :rows="6"
            placeholder="请求参数将显示在这里"
            readonly
            class="json-display"
        />
      </el-form-item>

      <!-- 通道响应文本域 -->
      <el-form-item label="通道响应">
        <el-input
            :value="JSON.stringify(testResult.channel_response, null, 2)"
            type="textarea"
            :rows="6"
            placeholder="通道响应将显示在这里"
            readonly
            class="json-display"
        />
      </el-form-item>

      <!-- 二维码显示 -->
      <div v-if="qrCodeUrl" class="qrcode-container">
        <h4>支付二维码</h4>
        <img :src="qrCodeUrl" alt="QRCode" />
      </div>
    </div>
  </el-drawer>
</template>

