  width: 200px;
  height: 200px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.result-section {
  margin: 20px 0;
}

.result-section h4 {
  margin-bottom: 10px;
  color: #333;
  font-size: 14px;
  font-weight: 600;
}

.json-display {
  font-family: 'Courier New', monospace;
  font-size: 12px;
}

.json-display :deep(.el-textarea__inner) {
  background-color: #f8f9fa;
  border: 1px solid #e9ecef;
  color: #495057;
